import { bridge } from '@bridge';
import { changePageStatus } from '@api';
import {
  PageStatus,
  ScreenOrientation,
  AdOperationType,
  DEFAULT_APP_NAME,
  H5_DEFAULT_ICON_URL,
  DOWNLOAD_DEFAULT_ICON_URL,
  COVER_DEFAULT_URL,
} from '@enum';
import { getIcon, getImgUrl, isIOS, radarStart, radarEnd } from '@util';
import { BtnTexts, AdInfo } from '@interface';
import { error } from '@log';

interface ObjectProps {
  [key: string]: any;
}

const formatAdData = ({ matrixType, univData, isPorttrait, deviceInfo, containerLimit }) => {
  const {
    adStyle,
    adBaseInfo,
    adMaterialInfo,
    advertiserInfo,
    adConversionInfo,
    downloadSafeInfo,
    adMatrixInfo = {},
    materialFeature,
    ocpcActionType,
    adStyleInfo = {},
  } = univData;
  const appName = adBaseInfo?.appName;
  const rawUserName = advertiserInfo?.rawUserName;
  const userName = advertiserInfo?.userName;
  const portraitUrl = advertiserInfo?.portraitUrl;
  const productName = adBaseInfo?.productName;
  const appIconUrl = adBaseInfo?.appIconUrl;
  const { slideClick } = adStyleInfo;

  // 
  const isDownload = Number(adBaseInfo?.adOperationType) === AdOperationType.DOWNLOAD;

  /**
   * adTitle 
   *  appName > rawUserName > 
   *  productName > rawUserName > 
   */
  const adTitle = isDownload
    ? appName || rawUserName || DEFAULT_APP_NAME
    : productName || rawUserName || DEFAULT_APP_NAME;
  /**
   * adAppUrl 
   *  appIconUrl > portraitUrl > ICON
   *  portraitUrl > ICON
   */
  const adAppUrl = isDownload
    ? appIconUrl || portraitUrl || DOWNLOAD_DEFAULT_ICON_URL
    : portraitUrl || H5_DEFAULT_ICON_URL;

  // 
  const isVideo = Number(adMaterialInfo?.materialType) === 1;
  // 
  const imageSize = adMaterialInfo?.materialFeature?.[0]?.materialSize;
  const isVerticalMaterial = imageSize?.width < imageSize?.height;
  const appScore = Number(adBaseInfo?.appScore) || 0;
  const appDownloadCountDesc = adBaseInfo?.appDownloadCountDesc;

  const playableUrl = adConversionInfo?.playableUrl || '';

  const playableOrientation = adConversionInfo?.playableStyleInfo?.playableOrientation || 0;

  let appImageUrl = adBaseInfo?.appImageUrl;
  let appImageSize = adBaseInfo?.appImageSize;
  if (!Array.isArray(appImageUrl) || (Array.isArray(appImageUrl) && !appImageUrl.length)) {
    appImageUrl = [materialFeature?.coverUrl];
    appImageSize = materialFeature?.materialSize;
  }
  const materialInfo = adMaterialInfo?.materialFeature?.[0];
  const adCoverUrl = materialInfo?.coverUrl || COVER_DEFAULT_URL;
  const adDescription = adBaseInfo?.adDescription;
  let appDescription = adBaseInfo?.appDescription;
  appDescription = appDescription ? appDescription.replace(/\n/g, '<br/>') : adDescription || '';

  let packageSize = adBaseInfo?.packageSize ? Number(adBaseInfo?.packageSize) : 0;
  if (Number.isNaN(packageSize)) {
    packageSize = 0;
  }
  let permissionInfo = downloadSafeInfo?.permissionInfo || '';
  permissionInfo = permissionInfo && permissionInfo.replace(/\n/g, '<br/>');

  const cardData = adStyleInfo?.playEndInfo?.adWebCardInfo?.cardData;

  const leftButtonTexts: BtnTexts = {
    adActionDescription:
      typeof cardData === 'string' ? JSON.parse(cardData).leftButtonText : cardData.leftButtonText,
    installAppLabel: adBaseInfo?.installAppLabel || '',
    openAppLabel: adBaseInfo?.openAppLabel || '',
  };

  const texts: BtnTexts = {
    adActionDescription: adBaseInfo?.adActionDescription || (isDownload ? '' : ''),
    installAppLabel: adBaseInfo?.installAppLabel || '',
    openAppLabel: adBaseInfo?.openAppLabel || '',
  };
  const { matrixData, matrixLayout } = getMatrixData({ adMatrixInfo, matrixType, isPorttrait });

  let exposeTagInfoList = [];

  try {
    exposeTagInfoList = adStyleInfo?.extraDisplayInfo?.exposeTagInfoList || [];
    exposeTagInfoList = exposeTagInfoList.filter((item) => item.text);
  } catch (err) {
    console.error(err);
  }

  const info: AdInfo = {
    // 
    adStyle,
    // 
    adTitle,
    adAppUrl,
    adDescription: adBaseInfo?.adDescription,
    adActionDescription: adBaseInfo?.adActionDescription,
    adMarkIcon: adBaseInfo?.adMarkIcon,
    adGrayMarkIcon: adBaseInfo?.adGrayMarkIcon,
    adSourceDescription: adBaseInfo?.adSourceDescription,
    adSourceType: adBaseInfo?.adSourceType,
    adIconInfo: getIcon(adBaseInfo),
    texts,
    leftButtonTexts,

    // 
    videoUrl: adMaterialInfo?.materialFeature?.[0]?.materialUrl,
    coverUrl: adCoverUrl,
    videoDuration: adMaterialInfo?.materialFeature?.[0]?.videoDuration || 0,
    materialFeature: adMaterialInfo?.materialFeature,
    materialType: adMaterialInfo?.materialType,
    imageUrl: getImgUrl(adMaterialInfo),

    // app
    appName,
    appIconUrl,
    productName,
    appScore,
    appDownloadCountDesc,
    appVersion: adBaseInfo?.appVersion,
    packageSize,
    appImageUrl,
    appImageSize,
    appCategory: adBaseInfo?.appCategory,
    appDescription,
    exposeTagInfoList,

    // 
    rawUserName,
    userName,
    portraitUrl,
    corporationName: adBaseInfo?.corporationName,

    // 
    isDownload,
    isVideo,
    isPorttrait,
    isVerticalMaterial,
    isIOS,
    slideClick,

    // 
    playableUrl,
    playableOrientation,

    // 
    downloadSafeInfo,
    titleBarTextSwitch: downloadSafeInfo?.complianceInfo?.titleBarTextSwitch || 0,
    appPermissionInfoUrl: downloadSafeInfo?.appPermissionInfoUrl || '',
    appPrivacyUrl: downloadSafeInfo?.appPrivacyUrl || '',
    permissionInfo,

    // matrix
    adMatrixInfo,
    matrixLayout,

    // 
    ocpcActionType,

    extra: {
      // 
      containerHeight: containerLimit.containerHeight,
      containerWidth: containerLimit.containerWidth,
      // SDK
      SDKVersion: deviceInfo.SDKVersion,
      SDKVersionCode: deviceInfo.SDKVersionCode,
      sdkType: deviceInfo.sdkType,
      screenWidth: deviceInfo.screenWidth,
      screenHeight: deviceInfo.screenHeight,
      systemVersion: deviceInfo.systemVersion,
    },
  };
  for (const i in info) {
    info[i] =
      typeof matrixData[i] !== 'undefined' && matrixData[i] !== null ? matrixData[i] : info[i];
  }
  return info;
};

export const fetchData = async ({ mockName = '', matrixType = '' }) => {
  // 
  radarStart('getKsAdDataFail');
  radarStart('getKsAdConfigFail');
  radarStart('getScreenOrientationFail');
  radarStart('getDeviceInfoFail');
  radarStart('getContainerLimitFail');
  return Promise.allSettled([
    bridge.getKsAdData(mockName),
    bridge.getKsAdConfig(),
    bridge.getScreenOrientation(),
    bridge.getDeviceInfo(),
    bridge.getContainerLimit(),
  ]).then((res: any) => {
    let ksAdConfig: ObjectProps = {};
    let isPorttrait = true;
    let deviceInfo: ObjectProps = {};
    let containerLimit: ObjectProps = {};
    if (res[0].status !== 'fulfilled') {
      radarEnd('getKsAdDataFail');
      error('getKsAdData fail', { type: 'bridge' });
      changePageStatus(PageStatus.ERROR);
    }
    if (res[1].status !== 'fulfilled') {
      radarEnd('getKsAdConfigFail');
      error('getKsAdConfig fail', { type: 'bridge' });
    } else {
      ksAdConfig = res[1].value.data;
    }
    if (res[2].status !== 'fulfilled') {
      radarEnd('getScreenOrientationFail');
      error('getScreenOrientation fail', { type: 'bridge' });
    } else {
      isPorttrait = res[2].value.data.screenOrientation === ScreenOrientation.PORTTRAIT;
    }
    if (res[3].status !== 'fulfilled') {
      radarEnd('getDeviceInfoFail');
      error('getDeviceInfo fail', { type: 'bridge' });
    } else {
      deviceInfo = res[3].value.data;
    }
    if (res[4].status !== 'fulfilled') {
      radarEnd('getContainerLimitFail');
      error('getContainerLimit fail', { type: 'bridge' });
    } else {
      containerLimit = res[4].value.data;
    }
    if (res[0].status === 'fulfilled') {
      const impAdInfo = res[0].value.data.impAdInfo || [];
      const adInfos = impAdInfo
        .filter((item) => {
          return item.adInfo;
        })
        .map((item) => {
          return { ...item.adInfo[0], adStyle: item.adStyle };
        });
      if (!adInfos.length) {
        error('getKsAdData fail', { type: 'bridge' });
        changePageStatus(PageStatus.ERROR);
      }
      const infoList: AdInfo[] = adInfos.map((item) => {
        const info: AdInfo = formatAdData({
          matrixType,
          univData: item,
          isPorttrait,
          deviceInfo,
          containerLimit,
        });
        return info;
      });
      return {
        info: infoList[0],
        infoList,
        ksAdConfig,
      };
    }
  });
};

export const getMatrixData = ({ adMatrixInfo, matrixType, isPorttrait }) => {
  const styleInfo = adMatrixInfo?.adDataV2?.[matrixType] || {};
  const layout = adMatrixInfo?.layout?.[matrixType] || {};
  const matrixLayoutString = isPorttrait ? layout?.v : layout?.h; // json

  const { templateId = '' } = styleInfo;
  const templateDatas = adMatrixInfo?.adDataV2?.templateDatas || [];
  const matrixDataString =
    templateDatas?.find((templateData) => {
      return templateData?.templateId === templateId;
    })?.data || '{}';
  let matrixData;
  try {
    matrixData = JSON.parse(matrixDataString);
  } catch (e) {
    matrixData = {};
  }
  let matrixLayout;
  try {
    matrixLayout = JSON.parse(matrixLayoutString);
  } catch (e) {
    matrixLayout = null;
  }
  return { matrixData, matrixLayout };
};

export const registerLifecycleListener = async (): Promise<{
  lifeStatus: 'showStart' | 'hideStart' | 'showEnd' | 'hideEnd';
}> => {
  return new Promise((resolve, reject) => {
    bridge.registerLifecycleListener({
      success(res) {
        resolve(res.data);
      },
      error(e) {
        reject(e);
      },
    });
  });
};
