import { error } from '@log';
import { MaterialType, COVER_DEFAULT_URL } from '@enum';

export * from './native';
export * from './radar';

export const initDevideInfo = (info) => {
    const deviceInfoInit = {
        SDKVersion: '0.0.0', // sdk3.3.20
        SDKVersionCode: 0, // sdk3032000 int
        SDKApiVersion: '0.0.0', // sdk 3.3.19(iOS )
        SDKApiVersionCode: 0, // sdk 3031900 int(iOS )
        sdkType: 0, // SDK1SDK2SDK
        appVersion: '1.0', // app
        appName: '', // appName
        appId: '', // appId appid
        globalId: '', // ''"eGid
        eGid: '', // gid
        deviceSig: '',
        networkType: '', // 
        manufacturer: '', //  (iOS )
        model: '', // 
        deviceBrand: '', // (iOS)
        osType: 0, //  1Android 2ios
        systemVersion: '', // 
        osApi: 0, // api(iOS )
        language: '', // 
        locale: '', // 
        uuid: '', // deviceId ANDROID_+androidId
        screenWidth: window.screen.width, // 
        screenHeight: window.screen.height, // 
        oaid: '', // oiad(iOS )
        androidId: '', // androidId(iOS )
        mac: '', // mac(iOS)
        statusBarHeight: 0, // 
        titleBarHeight: 0, // 
    };
    return { ...deviceInfoInit, ...info };
};

export function getClientOSType() {
    const uaString = navigator.userAgent;
    if (/Android/.test(uaString)) {
        return 'Android';
    }
    if (/(iPhone|iPad)/.test(uaString)) {
        return 'iOS';
    }
    return '';
}

export const isIOS = (() => getClientOSType().toLowerCase() === 'ios')();

export const dpr = isIOS ? 1 : window.devicePixelRatio;

// 
export const getImgUrl = (info) => {
    const { materialType } = info;
    const materialFeature = info.materialFeature[0];
    const { coverUrl, materialUrl, firstFrame } = materialFeature;
    if (materialType === MaterialType.IMAGE && materialUrl) {
        return materialUrl;
    }
    if (coverUrl) {
        return coverUrl;
    }
    if (firstFrame) {
        return firstFrame;
    }
    return 'COVER_DEFAULT_URL';
};

// ICON
export const getIcon = (info) => {
    return {
        adMarkIcon: info.adMarkIcon,
        adSourceDescription: info.adSourceDescription,
        adSourceType: info.adSourceType,
        adGrayMarkIcon: info.adGrayMarkIcon,
    };
};

export function scoreCeil(score: number, baseline: number = 3) {
    return Math.ceil(Math.max(score, baseline) / 0.5) * 0.5;
}
